import server from '../../server.js';
export default {
	data() {
		return {
			list:[]
		}
	},
	onLoad:function(e){
		console.log(e.id)
		this.getList(e.id);
	},
	methods: {
		getList:function(postid){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=getDashangList&postId="+postid, //仅为示例，并非真实接口地址。
				data: {
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.list=res.data.data
						this.$forceUpdate()
					}else{
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
			
		}
	}
}

